/* ===============================
   SIGN UP PAGE â€“ FINAL FIX
   =============================== */

.SignUp {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: radial-gradient(
    circle at left,
    #2b9cd8,
    #0f172a 45%,
    #064e3b
  );
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 1.5rem;
}

/* Glass Card */
.SignUpContainer {
  background: rgba(15, 23, 42, 0.9);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 25px 55px rgba(0, 0, 0, 0.6);
  width: 100%;
  max-width: 500px;
  padding: 2.2rem 2.5rem;

  /* ðŸ”¥ IMPORTANT: NO SCROLL */
  max-height: none;
  overflow: hidden;

  backdrop-filter: blur(14px);
  animation: slideIn 0.5s ease-out;
}

/* Animation */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(18px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Heading */
.SignUpContainer h2 {
  font-size: 2.2rem;
  font-weight: 700;
  color: #e5e7eb;
  text-align: center;
  margin-bottom: 0.3rem;
}

/* Login link */
.login-link {
  text-align: center;
  color: #9ca3af;
  margin-bottom: 1.6rem;
  font-size: 0.95rem;
}

.signin-btn-link {
  background: #3b82f6;
  color: #020617;
  padding: 0.45rem 1.1rem;
  border: none;
  border-radius: 8px;
  margin-left: 0.4rem;
  font-weight: 600;
  cursor: pointer;
}

.signin-btn-link:hover {
  background: #60a5fa;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
}

/* Form Group */
.form-group {
  margin-bottom: 1.1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.35rem;
  color: #cbd5f5;
  font-weight: 600;
  font-size: 0.9rem;
}

/* Inputs */
.form-group input {
  width: 100%;
  padding: 0.85rem 1rem;
  background: #020617;
  border: 1px solid #334155;
  border-radius: 10px;
  font-size: 0.95rem;
  color: #e5e7eb;
}

.form-group input::placeholder {
  color: #64748b;
}

.form-group input:focus {
  outline: none;
  border-color: #38bdf8;
  box-shadow: 0 0 0 2px rgba(56, 189, 248, 0.25);
}

/* Error input */
.form-group input.error {
  border-color: #ef4444;
}

/* Password Field */
.password-input {
  position: relative;
}

.eye-icon {
  position: absolute;
  right: 0.9rem;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  font-size: 1.1rem;
  color: #9ca3af;
}

.eye-icon:hover {
  color: #e5e7eb;
}

/* Error text */
.field-error {
  color: #f87171;
  font-size: 0.8rem;
  margin-top: 0.2rem;
}

/* ===============================
   FIXED CHECKBOX (IMPORTANT)
   =============================== */

.terms-conditions {
  margin: 1.2rem 0 1.5rem;
}

.terms-conditions label {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  font-size: 0.85rem;
  color: #9ca3af;
  line-height: 1.4;
}

.terms-conditions input[type="checkbox"] {
  width: 16px;
  height: 16px;
  accent-color: #38bdf8;
  cursor: pointer;
}

/* Links */
.terms-link {
  color: #38bdf8;
  text-decoration: none;
  font-weight: 600;
}

.terms-link:hover {
  text-decoration: underline;
}

/* Submit Button */
.signup-btn {
  width: 100%;
  padding: 0.95rem;
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  color: #020617;
  font-size: 1.05rem;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  margin-bottom: 1.2rem;
}

.signup-btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 12px 25px rgba(34, 197, 94, 0.4);
}

.signup-btn:disabled {
  opacity: 0.6;
}

/* Divider */
.divider {
  position: relative;
  text-align: center;
  margin: 1.2rem 0;
  color: #9ca3af;
  font-size: 0.85rem;
}

.divider::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: #334155;
}

.divider span {
  position: relative;
  background: rgba(15, 23, 42, 0.95);
  padding: 0 0.8rem;
}

/* Google Button */
.google-btn {
  width: 100%;
  padding: 0.9rem;
  background: #020617;
  border: 1px solid #334155;
  border-radius: 12px;
  color: #e5e7eb;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.7rem;
}

.google-btn:hover {
  border-color: #38bdf8;
  transform: translateY(-1px);
}

.google-btn img {
  width: 18px;
}
