/* Page wrapper */
.welcome-wrapper {
  min-height: calc(100vh - 70px);
  background: linear-gradient(180deg, #f8fafc, #eef2ff);
  padding: 4rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Main card */
.welcome-card {
  background: #ffffff;
  max-width: 900px;
  width: 100%;
  padding: 3.5rem;
  border-radius: 28px;
  box-shadow: 0 25px 60px rgba(0, 0, 0, 0.08);
  text-align: center;
  margin-bottom: 4rem;
}

/* Title */
.welcome-title {
  font-size: 3rem;
  font-weight: 800;
  color: #0f172a;
  margin-bottom: 1rem;
}

.welcome-title span {
  color: #2563eb;
}

/* Subtitle */
.welcome-subtitle {
  font-size: 1.15rem;
  color: #475569;
  margin-bottom: 2.8rem;
}

/* Buttons */
.welcome-actions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.btn-primary {
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  color: #ffffff;
  padding: 1rem 2.2rem;
  font-size: 1.05rem;
  font-weight: 600;
  border: none;
  border-radius: 999px;
  cursor: pointer;
  box-shadow: 0 12px 25px rgba(37, 99, 235, 0.35);
  transition: all 0.3s ease;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 18px 40px rgba(37, 99, 235, 0.45);
}

.btn-secondary {
  background: #e0e7ff;
  color: #1d4ed8;
  padding: 1rem 2.2rem;
  font-size: 1.05rem;
  font-weight: 600;
  border: none;
  border-radius: 999px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-secondary:hover {
  background: #c7d2fe;
  transform: translateY(-2px);
}

/* Feature cards */
.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  width: 100%;
}

.feature-card {
  background: #ffffff;
  padding: 2rem;
  border-radius: 22px;
  text-align: center;
  box-shadow: 0 18px 45px rgba(0, 0, 0, 0.06);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-6px);
}

.feature-card h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 0.6rem;
}

.feature-card p {
  font-size: 1rem;
  color: #64748b;
}

/* Responsive */
@media (max-width: 768px) {
  .welcome-title {
    font-size: 2.2rem;
  }

  .welcome-card {
    padding: 2.5rem 1.8rem;
  }
}
