.todo-form-page {
  min-height: calc(100vh - 70px);
  display: flex;
  align-items: center;
  justify-content: center;
  background:
    radial-gradient(circle at top left, #38bdf8, transparent 40%),
    radial-gradient(circle at bottom right, #22c55e, transparent 40%),
    #020617;
}

.todo-card {
  width: 100%;
  max-width: 520px;
  padding: 2.8rem;
  border-radius: 24px;
  background: rgba(15, 23, 42, 0.95);
  border: 1px solid rgba(148, 163, 184, 0.35);
  backdrop-filter: blur(18px);
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.7);
  color: #e5e7eb;
}

.todo-card h1 {
  font-size: 2rem;
  margin-bottom: 0.4rem;
}

.subtitle {
  font-size: 0.9rem;
  color: #9ca3af;
  margin-bottom: 1.8rem;
}

.todo-card label {
  display: block;
  margin-top: 1.2rem;
  font-weight: 500;
}

.todo-card input {
  width: 100%;
  margin-top: 0.4rem;
  padding: 0.8rem;
  border-radius: 12px;
  border: 1px solid rgba(148, 163, 184, 0.5);
  background: rgba(15, 23, 42, 0.98);
  color: #e5e7eb;
}

.todo-card input:focus {
  outline: none;
  border-color: #38bdf8;
  box-shadow: 0 0 0 1px rgba(56, 189, 248, 0.4);
}

.todo-card button {
  margin-top: 2rem;
  width: 100%;
  padding: 0.9rem;
  border-radius: 999px;
  border: none;
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  color: #020617;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 16px 40px rgba(34, 197, 94, 0.5);
}
