:root {
  --bg-dark: #020617;
  --bg-card: rgba(15, 23, 42, 0.95);
  --text-main: #e5e7eb;
  --text-muted: #9ca3af;
  --accent: #38bdf8;
  --accent-2: #22c55e;
}

[data-theme="light"] {
  --bg-dark: #f8fafc;
  --bg-card: #ffffff;
  --text-main: #020617;
  --text-muted: #475569;
}

/* Header */
.app-header {
  height: 70px;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--bg-card);
  backdrop-filter: blur(18px);
  border-bottom: 1px solid rgba(148, 163, 184, 0.25);
  box-shadow: 0 10px 30px rgba(0,0,0,0.25);
}

/* Brand */
.header-left {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  cursor: pointer;
}

.brand-badge {
  width: 36px;
  height: 36px;
  border-radius: 10px;
  background: linear-gradient(135deg, var(--accent), var(--accent-2));
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: #020617;
}

.brand-text {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-main);
}

/* Nav */
.header-nav {
  display: flex;
  gap: 1rem;
}

.header-nav button {
  background: none;
  border: none;
  color: var(--text-muted);
  font-size: 0.95rem;
  cursor: pointer;
}

.header-nav button:hover {
  color: var(--accent);
}

/* Right */
.header-right {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

/* Theme */
.theme-toggle {
  border: none;
  background: none;
  font-size: 1.2rem;
  cursor: pointer;
}

/* Avatar */
.avatar-wrapper {
  position: relative;
}

.avatar {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--accent), var(--accent-2));
  color: #020617;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

/* Dropdown */
.avatar-menu {
  position: absolute;
  right: 0;
  top: 52px;
  width: 200px;
  padding: 0.7rem;
  border-radius: 14px;
  background: rgba(15, 23, 42, 0.98);
  border: 1px solid rgba(148, 163, 184, 0.35);
  box-shadow: 0 25px 60px rgba(0, 0, 0, 0.6);
  color: #e5e7eb; /* âœ… FIX */
  animation: fadeDown 0.18s ease-out;
}

.avatar-user {
  font-size: 0.85rem;
  color: #cbd5f5; /* âœ… FIX */
  margin-bottom: 0.4rem;
}

/* Dropdown buttons */
.avatar-menu button {
  width: 100%;
  background: none;
  border: none;
  padding: 0.55rem 0.6rem;
  text-align: left;
  font-size: 0.9rem;
  color: #e5e7eb; /* âœ… FIX */
  cursor: pointer;
  border-radius: 8px;
}

.avatar-menu button:hover {
  background: rgba(56, 189, 248, 0.15);
}

.avatar-menu .logout {
  color: #f87171;
}
.app-header {
  position: sticky;
  top: 0;
  z-index: 9999; /* ðŸ”¥ VERY IMPORTANT */
}

/* Avatar wrapper must allow overflow */
.avatar-wrapper {
  position: relative;
  overflow: visible;
}

/* Dropdown menu */
.avatar-menu {
  position: absolute;
  right: 0;
  top: 52px;
  z-index: 10000; /* ðŸ”¥ ABOVE TABLES */
}
@keyframes fadeDown {
  from {
    opacity: 0;
    transform: translateY(-6px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Header buttons (isolated, no Bootstrap conflict) */
.header-btn {
  padding: 0.45rem 1.2rem;
  border-radius: 999px;
  font-size: 0.9rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.header-login {
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  color: #020617;
  box-shadow: 0 10px 28px rgba(34, 197, 94, 0.5);
}

.header-login:hover {
  transform: translateY(-1px);
}
