/* Page background */
.todos-page {
  min-height: calc(100vh - 70px);
  background:
    radial-gradient(circle at top left, #38bdf8, transparent 45%),
    radial-gradient(circle at bottom right, #22c55e, transparent 45%),
    #020617;
  padding: 2.5rem 1.5rem;
  display: flex;
  justify-content: center;
}

/* Main card */
.todos-card {
  width: 100%;
  max-width: 1100px;
  background: rgba(15, 23, 42, 0.94);
  border-radius: 24px;
  border: 1px solid rgba(148, 163, 184, 0.35);
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.7);
  padding: 2rem;
  backdrop-filter: blur(18px);
  color: #e5e7eb;
  animation: fadeUp 0.5s ease;
}

/* Header */
.todos-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.todos-header h1 {
  font-size: 1.8rem;
  font-weight: 600;
}

/* Add button */
.add-task-btn {
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  color: #020617;
  border: none;
  border-radius: 999px;
  padding: 0.55rem 1.2rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 12px 30px rgba(34, 197, 94, 0.5);
}

.add-task-btn:hover {
  transform: translateY(-1px);
}

/* Success message */
.success-msg {
  background: rgba(34, 197, 94, 0.15);
  border: 1px solid rgba(34, 197, 94, 0.6);
  color: #bbf7d0;
  padding: 0.7rem;
  border-radius: 12px;
  margin-bottom: 1.2rem;
  text-align: center;
}

/* Table */
.table-wrapper {
  overflow-x: auto;
}

.todos-table {
  width: 100%;
  border-collapse: collapse;
}

.todos-table thead {
  background: rgba(30, 41, 59, 0.8);
}

.todos-table th {
  text-align: left;
  padding: 0.9rem;
  font-weight: 500;
  color: #cbd5f5;
}

.todos-table td {
  padding: 0.9rem;
  border-top: 1px solid rgba(148, 163, 184, 0.2);
}

.todos-table tbody tr:hover {
  background: rgba(148, 163, 184, 0.08);
}

/* Description */
.desc {
  font-weight: 500;
}

/* Status pill */
.status {
  padding: 0.25rem 0.7rem;
  border-radius: 999px;
  font-size: 0.8rem;
  font-weight: 600;
}

.status.pending {
  background: rgba(248, 113, 113, 0.15);
  color: #fecaca;
}

.status.done {
  background: rgba(34, 197, 94, 0.15);
  color: #bbf7d0;
}

/* Actions */
.actions {
  display: flex;
  gap: 0.4rem;
}

.icon-btn {
  border: none;
  border-radius: 8px;
  padding: 0.35rem 0.6rem;
  cursor: pointer;
  font-size: 0.9rem;
  background: rgba(148, 163, 184, 0.15);
}

.icon-btn.edit:hover {
  background: rgba(56, 189, 248, 0.25);
}

.icon-btn.delete:hover {
  background: rgba(239, 68, 68, 0.25);
}

/* Empty state */
.empty-state {
  text-align: center;
  padding: 3rem 1rem;
  color: #9ca3af;
}

.empty-state button {
  margin-top: 0.8rem;
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 999px;
  font-weight: 600;
  cursor: pointer;
}

/* Animation */
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(14px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .todos-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .actions {
    justify-content: flex-end;
  }
}
.todo-container0,
.todo-container,
.todo-table-wrapper {
  overflow: visible !important;
}