/* ===== Page Background ===== */
.todo-update-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 4rem 1.5rem;
  background:
    radial-gradient(circle at top left, #38bdf8, transparent 45%),
    radial-gradient(circle at bottom right, #22c55e, transparent 45%),
    #020617;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
}

/* ===== Card ===== */
.todo-update-container {
  width: 100%;
  max-width: 620px;
  background: rgba(15, 23, 42, 0.95);
  backdrop-filter: blur(18px);
  border-radius: 22px;
  padding: 2.8rem 2.6rem 3rem;
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.75);
  border: 1px solid rgba(148, 163, 184, 0.35);
  color: #e5e7eb;
  animation: rise 0.45s ease-out;
}

/* Animation */
@keyframes rise {
  from {
    opacity: 0;
    transform: translateY(18px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== Header ===== */
.todo-update-container h1 {
  font-size: 1.9rem;
  font-weight: 600;
  margin-bottom: 0.4rem;
}

.todo-update-container .subtitle {
  font-size: 0.95rem;
  color: #9ca3af;
  margin-bottom: 2.2rem;
}

/* ===== Form ===== */
.todo-update-form {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

/* ===== Form Group ===== */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.45rem;
}

.form-group label {
  font-size: 0.9rem;
  font-weight: 500;
  color: #cbd5f5;
}

/* ===== Inputs ===== */
.form-group input[type="text"],
.form-group input[type="date"] {
  width: 100%;
  padding: 0.8rem 0.9rem;
  font-size: 0.95rem;
  border-radius: 14px;
  border: 1px solid rgba(148, 163, 184, 0.45);
  background: rgba(15, 23, 42, 0.98);
  color: #e5e7eb;
  outline: none;
  transition: border 0.2s ease, box-shadow 0.2s ease, transform 0.1s ease;
}

.form-group input::placeholder {
  color: #64748b;
}

.form-group input:focus {
  border-color: #38bdf8;
  box-shadow: 0 0 0 1px rgba(56, 189, 248, 0.4),
              0 0 28px rgba(56, 189, 248, 0.25);
  transform: translateY(-1px);
}

/* ===== Checkbox ===== */
.checkbox-group {
    
  flex-direction: row;
  align-items: center;
  gap: 0.6rem;
  margin-top: 0.3rem;
}

.checkbox-group input {
  width: 16px;
  height: 16px;
  accent-color: #38bdf8;
}

.checkbox-group label {
  font-size: 0.9rem;
  font-weight: 400;
  color: #cbd5f5;
  margin: 0;
}

/* ===== Buttons ===== */
.button-row {
  display: flex;
  gap: 1rem;
  margin-top: 1.4rem;
}

/* Primary */
.update-button {
  flex: 1;
  padding: 0.9rem;
  border-radius: 999px;
  border: none;
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  color: #020617;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  box-shadow: 0 16px 40px rgba(34, 197, 94, 0.5);
  transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.update-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 22px 50px rgba(34, 197, 94, 0.65);
}

/* Secondary */
.cancel-button {
  flex: 1;
  padding: 0.9rem;
  border-radius: 999px;
  background: rgba(148, 163, 184, 0.15);
  border: 1px solid rgba(148, 163, 184, 0.45);
  color: #e5e7eb;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.15s ease;
}

.cancel-button:hover {
  background: rgba(148, 163, 184, 0.25);
}

/* ===== Mobile ===== */
@media (max-width: 520px) {
  .todo-update-container {
    padding: 2.2rem 1.8rem 2.4rem;
  }

  .todo-update-container h1 {
    font-size: 1.6rem;
  }
}
