.login-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background:
    radial-gradient(circle at top left, #38bdf8, transparent 45%),
    radial-gradient(circle at bottom right, #22c55e, transparent 45%),
    #020617;
  font-family: system-ui, sans-serif;
}

.login-card {
  width: 100%;
  max-width: 420px;
  padding: 2.8rem 2.4rem;
  border-radius: 22px;
  background: rgba(15, 23, 42, 0.92);
  border: 1px solid rgba(148, 163, 184, 0.35);
  backdrop-filter: blur(18px);
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.75);
  color: #e5e7eb;
  animation: rise 0.5s ease;
}

@keyframes rise {
  from {
    opacity: 0;
    transform: translateY(18px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-card h2 {
  font-size: 1.9rem;
  margin-bottom: 0.4rem;
}

.subtitle {
  font-size: 0.9rem;
  color: #9ca3af;
  margin-bottom: 1.6rem;
}

.subtitle button {
  background: none;
  border: none;
  color: #38bdf8;
  font-weight: 500;
  margin-left: 0.4rem;
  cursor: pointer;
}

label {
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 1.2rem;
  display: block;
}

input {
  width: 100%;
  padding: 0.75rem 0.8rem;
  margin-top: 0.4rem;
  border-radius: 12px;
  border: 1px solid rgba(148, 163, 184, 0.5);
  background: rgba(15, 23, 42, 0.95);
  color: #e5e7eb;
  outline: none;
}

input:focus {
  border-color: #38bdf8;
  box-shadow: 0 0 0 1px rgba(56, 189, 248, 0.4);
}

.password-box {
  position: relative;
}

.password-box span {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.2rem 0;
  font-size: 0.85rem;
}

.row button {
  background: none;
  border: none;
  color: #38bdf8;
  cursor: pointer;
}

.submit-btn {
  width: 100%;
  padding: 0.85rem;
  border-radius: 999px;
  border: none;
  background: linear-gradient(135deg, #38bdf8, #22c55e);
  color: #020617;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 16px 40px rgba(34, 197, 94, 0.5);
}

.error-msg {
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.6);
  color: #fecaca;
  padding: 0.6rem;
  border-radius: 10px;
  margin-bottom: 0.8rem;
  text-align: center;
}
.login-google-btn {
  width: 100%;
  height: 48px;
  margin-top: 1rem;

  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.6rem;

  background: #0a0a0a;
  border: 1px solid #050606;
  border-radius: 14px;

  color: #ecedf0;
  font-size: 0.95rem;
  font-weight: 500;

  cursor: pointer;
  transition:
    background 0.15s ease,
    box-shadow 0.15s ease,
    transform 0.1s ease;
}

.login-google-btn:hover {
  background: #090a0a;
  box-shadow: 0 2px 6px rgba(0,0,0,0.12);
}

.login-google-btn:active {
  transform: scale(0.985);
}

.login-divider {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  margin: 1.2rem 0 0.6rem;
  color: #9ca3af;
  font-size: 0.8rem;
}

.login-divider::before,
.login-divider::after {
  content: "";
  flex: 1;
  height: 1px;
  background: rgba(148, 163, 184, 0.35);
}
.google-icon {
  width: 18px;
  height: 18px;
  background-image: url("https://www.gstatic.com/firebasejs/ui/2.0.0/images/auth/google.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  flex-shrink: 0;
}
